import React, { useEffect, useState } from 'react';
import { useLogoutMutation } from '../generated/graphql';
import { setAccessToken } from '../accessToken';
import { Sidenav } from './Sidenav';
import { MobileSideNav } from './MobileSideNav';

interface Props {
    children?: any
}

export const UI: React.FC<Props> = ({ children }) => {
    const [LOGOUT, { client }] = useLogoutMutation();
    const [not, setNot] = useState(null);
    
    useEffect(() => {
        var elems = document.querySelectorAll('.dropdown-trigger');
        M.Dropdown.init(elems, {
            hover: true,
        });
    })

    const logout = async () => {
        await LOGOUT();
        setAccessToken("");
        await client!.resetStore();
        window.location.reload();
    }

    return (
        <div id="navWrapper" className="navWrapper">
            <span id="NAVBAR" className="z-depth-4 noselect">
                <ul id="user-actions" className="dropdown-content">
                    <li>
                        <a href="#!" onClick={() => { logout(); }}>
                            Logout
                            <i className="material-icons">exit_to_app</i>
                        </a>
                    </li>
                </ul>

                <ul id="notifications" className="dropdown-content">
                    <li></li>
                </ul>
                <nav>
                    <div className="nav-wrapper">
                        <a href="#!" className="brand-logo"><i className="material-icons">bug_report</i> <b>Bug Tracker</b></a>

                        <a href="#" data-target="slide-out" className="sidenav-trigger"><i className="material-icons">menu</i></a>
                        <ul id="nav-mobile" className="right hide-on-med-and-down">
                            <li>
                                <a className="dropdown-trigger" href="#!" data-target="notifications">
                                    NOTIFICATIONS { !not ? 
                                        <i className="material-icons right">notifications_none</i> 
                                        :
                                        <i className = "material-icons">notifications_active</i> 
                                    }
                                </a>
                            </li>

                            <li>
                                <a className="dropdown-trigger" href="#!" data-target="user-actions">
                                    USER ACTIONS <i className="material-icons right">arrow_drop_down</i>
                                </a>
                            </li>

                        </ul>
                    </div>
                </nav>
            </span>
            <ul id="slide-out" className="sidenav">
                <span id="MOBILE_SIDENAV" className="z-depth-4">
                    <MobileSideNav />
                </span>
            </ul>

            <span id="SIDENAV" className="z-depth-4">
                <Sidenav />
            </span>

            <span id="CONTENT">
                {children ? children : null}
            </span>
        </div>
    );
}