import React, { useState, useEffect } from "react";
import { Doughnut, Bar, Pie } from 'react-chartjs-2';
import M from "materialize-css";
import { useGetTickets } from "./useGetTickets";
import { useGetUserTickets } from "./useGetUserTickets";
import { changeIndicatorP, changeIndicatorC, changeIndicatorT } from "./Indicators";

export const Dashboard: React.FC = () => {
    let LSname: any = localStorage.getItem('chartType');
    if (!!LSname) { LSname = LSname!.split(','); }
    let LStitle: any = localStorage.getItem('title');
    if (!!LStitle) { LStitle = LStitle!.split(','); }
    let LSchart: any = localStorage.getItem('chart');
    if (!!LSchart) { LSchart = LSchart.split(','); }
    let LSdata: any = localStorage.getItem('data');
    if (!!LSdata) {
        LSdata = JSON.parse(LSdata);
    }
    const tickets = useGetTickets() as [{ priority: string, status: string, type: string }];
    const userTickets = useGetUserTickets() as [{ priority: string, status: string, type: string }];
    const [state, setState] = useState({ Cards: LSchart || [1] });
    const [chart, setChart] = useState({ name: LSname || [] as string[] });
    const [title, setTitle] = useState({ title: LStitle || [] as string[] });
    const [chartData, setChartData] = useState({
        chart:
        [
            {

            }
        ]
    })

    useEffect(() => {
        M.AutoInit();
        var elems = document.querySelectorAll('.fixed-action-btn');
        M.FloatingActionButton.init(elems, {
            hoverEnabled: false
        });
        elems = document.querySelectorAll('.tooltipped');
        M.Tooltip.init(elems, {
            position: 'left'
        });
    })
    const AddCard = (): void => {
        let temp = state.Cards[state.Cards.length - 1];
        let t1 = state.Cards.concat(temp! += 1);
        setState({ ...state, Cards: state.Cards.concat(temp! += 1) });
        setChart({
            ...chart,
            name: chart.name.concat('')
        })
        localStorage.setItem('chart', `${t1}`);
    }

    const setChartType = (e: React.SyntheticEvent, i: number): void => {
        let target = e.target as any;
        let name = target.name;
        let tmp = chart.name;
        tmp[i] = name;
        if (!!name) {
            setChart({
                ...chart,
                name: tmp
            });
            localStorage.setItem('chartType', `${tmp}`)
        }
    }

    if (!tickets.length || !userTickets.length) {
        return (
            <div className="progress">
                <div className="indeterminate"></div>
            </div>
        )
    }

    const getCircularReplacer = () => {
        const seen = new WeakSet();
        return (_key: any, value: any) => {
            if (typeof value === "object" && value !== null) {
                if (seen.has(value)) {
                    return;
                }
                seen.add(value);
            }
            return value;
        };
    };

    const myTicketsByP = (i: number) => {
        var h = 0, m = 0, l = 0;

        for (let i = 0; i < userTickets.length; i++) {
            switch (userTickets[i].priority) {
                case 'high': {
                    h++;
                    break;
                }

                case 'medium': {
                    m++;
                    break;
                }

                case 'low': {
                    l++;
                    break;
                }
            }
        }
        let tmp = chartData.chart;
        let val = {
            labels: ['High', 'Medium', 'Low'],
            datasets: [
                {
                    label: 'Amount of Tickets',
                    data: [h, m, l, 0],
                    backgroundColor: [
                        '#051937',
                        '#A8EB12',
                        '#5FFBF1',
                        '#E13296',
                        '#E0DF31',
                    ],
                    borderWidth: 2
                }

            ]
        };
        tmp[i] = val;
        localStorage.setItem('data', JSON.stringify(tmp, getCircularReplacer()));
        let t = title.title;
        t[i] = 'My Tickets By Priority';
        setTitle({ title: t });
        localStorage.setItem('title', `${t}`);
    }

    const TicketsByP = (i: number) => {
        var h = 0, m = 0, l = 0;

        for (let i = 0; i < tickets.length; i++) {
            switch (tickets[i].priority) {
                case 'high': {
                    h++;
                    break;
                }

                case 'medium': {
                    m++;
                    break;
                }

                case 'low': {
                    l++;
                    break;
                }
            }
        }

        let tmp = chartData.chart;
        let val = {
            labels: ['High', 'Medium', 'Low'],
            datasets: [
                {
                    label: 'Amount of Tickets',
                    data: [h, m, l, 0],
                    backgroundColor: [
                        '#051937',
                        '#A8EB12',
                        '#5FFBF1',
                        '#E13296',
                        '#E0DF31',
                    ],
                    borderWidth: 2
                }
            ]
        };
        tmp[i] = val;
        localStorage.setItem('data', JSON.stringify(tmp, getCircularReplacer()));
        let t = title.title;
        t[i] = 'All Tickets By Priority';
        setTitle({ title: t });
        localStorage.setItem('title', `${t}`);
    }

    const myTicketsByS = (i: number) => {
        var o = 0, c = 0, h = 0;
        for (let i = 0; i < userTickets.length; i++) {
            switch (userTickets[i].status) {
                case 'open': {
                    o++;
                    break;
                }

                case 'closed': {
                    c++;
                    break;
                }

                case 'on-hold': {
                    h++;
                    break;
                }
            }
        }

        let tmp = chartData.chart;
        let val = {
            labels: ['Open', 'Closed', 'On Hold'],
            datasets: [
                {
                    label: 'Amount of Tickets',
                    data: [o, c, h, 0],
                    backgroundColor: [
                        '#051937',
                        '#A8EB12',
                        '#5FFBF1',
                        '#E13296',
                        '#E0DF31',
                    ],
                    borderWidth: 2
                }
            ]
        };
        tmp[i] = val;
        localStorage.setItem('data', JSON.stringify(tmp, getCircularReplacer()));

        let t = title.title;
        t[i] = 'My Tickets By Status';
        setTitle({ title: t });
        localStorage.setItem('title', `${t}`);
    }

    const TicketsByS = (i: number) => {
        var o = 0, c = 0, h = 0;
        for (let i = 0; i < tickets.length; i++) {
            switch (tickets[i].status) {
                case 'open': {
                    o++;
                    break;
                }

                case 'closed': {
                    c++;
                    break;
                }

                case 'on-hold': {
                    h++;
                    break;
                }
            }
        }

        let tmp = chartData.chart;
        let val = {
            labels: ['Open', 'Closed', 'On Hold'],
            datasets: [
                {
                    label: 'Amount of Tickets',
                    data: [o, c, h, 0],
                    backgroundColor: [
                        '#051937',
                        '#A8EB12',
                        '#5FFBF1',
                        '#E13296',
                        '#E0DF31',
                    ],
                    borderWidth: 2
                }
            ]
        };
        tmp[i] = val;
        localStorage.setItem('data', JSON.stringify(tmp, getCircularReplacer()));

        let t = title.title;
        t[i] = 'All Tickets By Status';
        setTitle({ title: t });
        localStorage.setItem('title', `${t}`);
    }

    const TicketsByT = (i: number) => {
        var be = 0, f = 0;
        for (let i = 0; i < tickets.length; i++) {
            switch (tickets[i].type) {
                case 'features': {
                    f++;
                    break;
                }

                case 'bugs/errors': {
                    be++;
                    break;
                }
            }
        }

        let tmp = chartData.chart;
        let val = {
            labels: ['Bugs/Erros', 'Features'],
            datasets: [
                {
                    label: 'Amount of Tickets',
                    data: [be, f, 0],
                    backgroundColor: [
                        '#051937',
                        '#A8EB12',
                        '#5FFBF1',
                        '#E13296',
                        '#E0DF31',
                    ],
                    borderWidth: 2
                }
            ]
        };
        tmp[i] = val;
        localStorage.setItem('data', JSON.stringify(tmp, getCircularReplacer()));

        let t = title.title;
        t[i] = 'All Tickets By Type';
        setTitle({ title: t });
        localStorage.setItem('title', `${t}`);
    }

    const myTicketsByT = (i: number) => {
        var be = 0, f = 0;
        for (let i = 0; i < userTickets.length; i++) {
            switch (userTickets[i].type) {
                case 'features': {
                    f++;
                    break;
                }

                case 'bugs/errors': {
                    be++;
                    break;
                }
            }
        }

        let tmp = chartData.chart;
        let val = {
            labels: ['Bugs/Erros', 'Features'],
            datasets: [
                {
                    label: 'Amount of Tickets',
                    data: [be, f, 0],
                    backgroundColor: [
                        '#051937',
                        '#A8EB12',
                        '#5FFBF1',
                        '#E13296',
                        '#E0DF31',
                    ],
                    borderWidth: 2
                }
            ]
        };
        tmp[i] = val;
        localStorage.setItem('data', JSON.stringify(tmp, getCircularReplacer()));

        let t = title.title;
        t[i] = 'My Tickets By Type';
        setTitle({ title: t });
        localStorage.setItem('title', `${t}`);
    }
return (
        <span>
            <div className="fixed-action-btn">
                <a className="btn-floating tooltipped btn-large" data-postion="left" data-tooltip="ADD A CHART" style={{ backgroundImage: 'linear-gradient(to right, #ff758c 0%, #ff7eb3 100%)' }} onClick={() => { AddCard() }}><i className="material-icons">insert_chart</i></a>

            </div>

            <div className="row">
                {state.Cards.map(function (_val: any, i: any) {
                    return (
                        <div className="col s11 m6 chart-lengthen" key={i}>
                            <div className="card">
                                <div className="card-content">
                                    <div className="row">
                                        <span className="card-title center">
                                            {title.title[i]}
                                        </span>
                                    </div>

                                    <div className="divider"></div>

                                    <div className="row row-verticle-center">
                                        <div className="col l12 m12 s12 centered">
                                            {chart.name[i] === 'Donut' && !!LSdata ? <Doughnut data={LSdata[i]} options={{ responsive: true }} /> : null}
                                            {chart.name[i] === 'Bar' && !!LSdata ? <Bar data={LSdata[i]} options={{ responsive: true }} /> : null}
                                            {chart.name[i] === 'Pie' && !!LSdata ? <Pie data={LSdata[i]} options={{ responsive: true }} /> : null}
                                        </div>
                                    </div>
                                </div>

                                <div className="card-tabs">
                                    <ul className="tabs tabs-fixed-width">
                                        <li className="tab"><a className="active" href={`#tab${i}`}>Chart Type</a></li>
                                        <li className="tab"><a href={`#tab2${i}`}>Tickets </a></li>
                                        <li className="tab"><a href={`#tab3${i}`}>Projects</a></li>
                                    </ul>
                                </div>

                                <div className="card-content grey lighten-4">
                                    <div id={`tab${i}`}>
                                        <div className="card-tabs">
                                            <ul id={`ul${i}`} className="tabs tabs-fixed-width">
                                                <li className="tab">
                                                    <a className="active" onClick={() => { changeIndicatorC(`${i}`, 'pink') }}>
                                                        <button className="card-opt" key={i} name={`Donut`} onClick={(e) => setChartType(e, i)}>  Donut </button>
                                                    </a>
                                                </li>
                                                <li className="tab" onClick={() => { changeIndicatorC(`${i}`, 'dark-blue') }}>
                                                    <a>
                                                        <button className="card-opt" key={i} name={`Pie`} onClick={(e) => { setChartType(e, i) }}>  Pie </button>
                                                    </a>
                                                </li>
                                                <li className="tab" onClick={() => { changeIndicatorC(`${i}`, 'cyan') }}>
                                                    <a>
                                                        <button className="card-opt" key={i} name={`Bar`} onClick={(e) => { setChartType(e, i) }}>  Bar </button>
                                                    </a>
                                                </li>
                                            </ul>
                                        </div>
                                    </div>

                                    <div id={`tab2${i}`}>
                                        <ul id={`ul2${i}`} className="tabs tabs-fixed-width">
                                            <li className="tab my-tickets"><a className="active" onClick={() => changeIndicatorT(`${i}`, 'yellow')} href={`#sub-tab2${i}`}>My Tickets</a></li>
                                            <li className="tab all-tickets"><a href={`#sub-tab2.1${i}`} onClick={() => changeIndicatorT(`${i}`, 'blue')}>All Tickets</a></li>
                                        </ul>
                                        <div className="card-content grey lighten-4">
                                            <div id={`sub-tab2${i}`}>
                                                <div className="card-tabs">
                                                    <ul id={`ul2${i}`} className="tabs tabs-fixed-width">
                                                        <li className="tab">
                                                            <a className="active yellow-text">
                                                                <button className="card-opt" key={i} onClick={() => { myTicketsByP(i) }}> By Priority </button>
                                                            </a>
                                                        </li>
                                                        <li className="tab">
                                                            <a>
                                                                <button className="card-opt" key={i} onClick={() => { myTicketsByS(i) }}> By Status </button>
                                                            </a>
                                                        </li>
                                                        <li className="tab">
                                                            <a>
                                                                <button className="card-opt" key={i} onClick={() => { myTicketsByT(i) }}> By Type </button>
                                                            </a>
                                                        </li>
                                                    </ul>
                                                </div>
                                            </div>

                                            <div id={`sub-tab2.1${i}`}>
                                                <div className="card-tabs">
                                                    <ul className="tabs tabs-fixed-width">
                                                        <li className="tab">
                                                            <a className="active">
                                                                <button className="card-opt" key={i} onClick={() => TicketsByP(i)}> By Priority </button>
                                                            </a>
                                                        </li>
                                                        <li className="tab">
                                                            <a>
                                                                <button className="card-opt" key={i} onClick={() => TicketsByS(i)}> By Status </button>
                                                            </a>
                                                        </li>
                                                        <li className="tab">
                                                            <a>
                                                                <button className="card-opt" key={i} onClick={() => TicketsByT(i)}> By Type </button>
                                                            </a>
                                                        </li>
                                                    </ul>
                                                </div>
                                            </div>
                                        </div>

                                    </div>

                                    <div id={`tab3${i}`}>
                                        <ul id={`ul3${i}`} className="tabs tabs-fixed-width">
                                            <li className="tab"><a className="active" href={`#sub-tab3${i}`} onClick={() => changeIndicatorP(`${i}`, 'orange')}>My Projects</a></li>
                                            <li className="tab"><a href={`#sub-tab3.1${i}`} onClick={() => changeIndicatorP(`${i}`, 'blue')}>All Projects</a></li>
                                        </ul>
                                        <div className="card-content grey lighten-4">
                                            <div id={`sub-tab3${i}`}>
                                                <div className="card-tabs">
                                                    <ul className="tabs tabs-fixed-width">
                                                        <li className="tab">
                                                            <a className="active">
                                                                <button className="card-opt" key={i}> By Priority </button>
                                                            </a>
                                                        </li>
                                                        <li className="tab">
                                                            <a>
                                                                <button className="card-opt" key={i}> By Status </button>
                                                            </a>
                                                        </li>
                                                        <li className="tab">
                                                            <a>
                                                                <button className="card-opt" key={i}> By Type </button>
                                                            </a>
                                                        </li>
                                                    </ul>
                                                </div>
                                            </div>

                                            <div id={`sub-tab3.1${i}`}>
                                                <div className="card-tabs">
                                                    <ul className="tabs tabs-fixed-width">
                                                        <li className="tab">
                                                            <a className="active">
                                                                <button className="card-opt" key={i}> By Priority </button>
                                                            </a>
                                                        </li>
                                                        <li className="tab">
                                                            <a>
                                                                <button className="card-opt" key={i}> By Status </button>
                                                            </a>
                                                        </li>
                                                        <li className="tab">
                                                            <a>
                                                                <button className="card-opt" key={i}> By Type </button>
                                                            </a>
                                                        </li>
                                                    </ul>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    );
                })}
            </div>
        </span>
    );
}