import React, { useState, useEffect } from 'react';
import { useLoginMutation, useDemoLoginMutation } from '../../generated/graphql';
import { RouteComponentProps } from 'react-router';
import { setAccessToken } from '../../accessToken';

export const Login: React.FC<RouteComponentProps> = () => {
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [login] = useLoginMutation();
    const [demoLogin] = useDemoLoginMutation();

    useEffect(() => {
        var elems = document.querySelectorAll('.dropdown-trigger');
        //eslint-disable-next-line
        var instances = M.Dropdown.init(elems);
    })

    const submitForm = async (e: any) => {
        e.preventDefault();
        if (email.length > 1 && password.length > 1) {
            const response = await login({
                variables: {
                    email,
                    password
                }
            })

            if (response && response.data) {
                setAccessToken(response.data.login.accessToken);
            }
            window.location.reload();
        } else if (password.length < 1 && email.length < 1) {
            document.getElementById('password')!.classList.add('invalid');
            document.getElementById('email')!.classList.add('invalid');
        } else if (password.length < 1) {
            document.getElementById('password')!.classList.add('invalid');
        }
    }

    const demoUser = async (e: React.SyntheticEvent, role: string) => {
        e.preventDefault();
        const response = await demoLogin({
            variables: {
                role
            }
        })
        
        if (response && response.data) {
            setAccessToken(response.data.demoLogin.accessToken);
        }
    }

    return (
        <div className="loginPageWrapper">
            <div className="container">
                <div className="center-align table-wrapper">
                    <span className="login-header z-depth-2">
                        <h2 className="white-text">Login</h2>
                    </span>

                    <div className="table-body z-depth-1">

                        <div className="row">
                            <div className="input-field">
                                <input id="email" className="validate" value={email} onChange={e => setEmail(e.target.value)} />
                                <label htmlFor="email">Email</label>
                                <span className="helper-text" data-error="Please enter a valid email"></span>
                            </div>
                        </div>

                        <div className="row">
                            <div className="input-field">
                                <input id="password" type="password" className="validate" value={password} onChange={e => setPassword(e.target.value)} />
                                <label htmlFor="password">Password</label>
                                <span className="helper-text" data-error="Please enter a valid password"></span>
                            </div>
                        </div>


                        <span className="buttons">
                            <a href="/login"><button type="button" className="btn stretched mb authAction" onClick={(e) => submitForm(e)}>Login</button></a>
                            <span className="centered mb"><span>OR</span></span>
                            <button type="submit" className="btn stretched mb authAction" onClick={() => { document.getElementById('dd')!.classList.remove('hide') }}>Demo User</button>
                            <span className='dropdown-trigger btn stretched manage-dropdown hide' id="dd" data-target='dropdownRole'>Role</span>
                            <ul id='dropdownRole' className='dropdown-content'>
                                <li>
                                    <button className="btnDropdown"
                                        onClick={(e) => {
                                            setEmail('demo@demo.com');
                                            setPassword('demoPassword');
                                            demoUser(e, 'admin');
                                        }}>
                                        Admin
                                            </button>
                                </li>

                                <li>
                                    <button className="btnDropdown"
                                        onClick={(e) => {
                                            setEmail('demo@demo.com');
                                            setPassword('demoPassword');
                                            demoUser(e, 'developer');
                                        }}>
                                        Developer
                                            </button>
                                </li>

                                <li>
                                    <button className="btnDropdown"
                                        onClick={(e) => {
                                            setEmail('demo@demo.com');
                                            setPassword('demoPassword');
                                            demoUser(e, 'project-manager');
                                        }}>
                                        Project Manager
                                            </button>
                                </li>

                            </ul>
                            <span className="centered mb"><span>OR</span></span>
                            <span className="centered">
                                <div><u><a href="/register">Sign Up Here</a></u></div>
                            </span>
                        </span>
                    </div>
                </div>
            </div>
        </div>
    );
}

